<template>
<div class="hello">
    <b-card>

        <b-row>
            <b-form-textarea id="textarea" v-model="sample" rows="10" max-rows="15" plaintext  disabled></b-form-textarea>
        </b-row>
        <b-row>
            <b-form-textarea id="textarea" v-model="enter" rows="10" max-rows="15" @keydown.tab.prevent="tabber($event)"></b-form-textarea>
        </b-row>
        <b-row>
            <b-col>
                <circular-count-down-timer v-if="isStart" :initial-value="countValue" :steps="300" :second-label="''" @finish="finished()"></circular-count-down-timer>
            </b-col>
            <b-col>
                <b-button @click="countTime()">Start</b-button>
            </b-col>
        </b-row>
    </b-card>

</div>
</template>

<script>
export default {
    name: "HelloWorld",
    props: {
        msg: String
    },
    data() {
        return {
            sample:`	An investigation by BioNTech has revealed that packaging defects in its COVID-19 vaccine vials were caused by the crimping process and ultra-low storage temperature but did not affect the jabs' safety.
	Director of Health Dr Constance Chan told a media session this afternoon that the Department of Health received a written notification from BioNTech today on its investigation into the packaging issue.
	Dr Chan said: "Upon further investigation, samples (of the BioNTech vaccine vials with packaging defects) detected in Hong Kong were sent back to Germany for an investigation.  They have done numerous tests and experiments on the vials concerned.  They have also done tests in another fill and finish plant operated by Baxter.
	Dr Chan said: "Upon further investigation, samples (of the BioNTech vaccine vials with packaging defects) detected in Hong Kong were sent back to Germany for an investigation.  They have done numerous tests and experiments on the vials concerned.  They have also done tests in another fill and finish plant operated by Baxter.
	"In the process, they found out that in the vials packed at the site which provided batches (numbered) 210102 and 210104, there were issues related to the so-called crimping process.  Crimping is the process by which pressure is applied to the aluminium ring which affixes to the rubber stopper at the top of the vial.
	"We received a report that the (defect in question) is related to the process of crimping and at the same time the condition of the ultra-low temperature of minus 70 degrees Celsius.  So that is why BioNTech has now decided to send the new vials from Baxter which have undergone repeated experiments and investigations, and that would exclude further occurrence of such an event.  "`,isStart: false,
            countValue: 300,
            enter: "",
        }
    },
    methods: {
        countTime() { 
            this.isStart = true
            this.countValue = 300
        },
        finished() {

            var original = this.sample.split(" ");
            var input = this.enter.split(" ");

            var originalTotal = original.length
            var inputTotal = 0;
            for(var i =0;i< input.length ;i++){ 
              if(original[i]== input[i]){
                inputTotal++
              }
            }
            alert(`Accuracy : ${inputTotal/originalTotal *100} % , WPM : ${inputTotal / 5}`)
            console.log(`Accuracy : ${inputTotal/originalTotal *100} % ,
                          WPM : ${inputTotal / 5}`)
            
            
        },
        tabber(event) {
            if (event) {
                event.preventDefault()
                let startText = this.enter.slice(0, event.target.selectionStart)
                let endText = this.enter.slice(event.target.selectionStart)
                this.enter = `${startText}\t${endText}`
                event.target.selectionEnd = event.target.selectionStart + 1
            }
        }
    }
};
</script>

<style lang="scss" scoped>
#editor {
    height: 350px;
}
</style>
